import { login, register, adminLogin } from 'api/auth';
import { createContext, useContext, useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';
import jwt_decode from 'jwt-decode';

const dummyUser = {
  id: 524,
  email: 'jennie@example.com',
  account: 'blackpink',
  name: 'Donut',
  avatar: '',
  coverUrl: '',
  introduction: 'In the area.',
  role: 'user',
  createdAt: '2023-03-27T12:42:24.000Z',
  updatedAt: '2023-03-27T12:42:24.000Z',
};
const defaultAuthContext = {
  isAuthenticated: false,
  currentMember: dummyUser,
  register: null,
  setting: null,
  login: null,
  adminLogin: null,
  logout: null,
};

//1. createContext
const AuthContext = createContext(defaultAuthContext);
// useContext  i.e.  useAuth  useContext hookuseAuth  hook
export const useAuth = () => useContext(AuthContext);
//2.  Context.Provider;  value AuthContext 
export const AuthProvider = ({ children }) => {
  // const [currentUser, setCurrentUser] = useState(dummyUser);

  const [isAuthenticated, setIsAuthenticated] = useState(false);
  // eslint-disable-next-line no-unused-vars
  const [payload, setPayload] = useState(null);
  const { pathname } = useLocation();

  // const handleUserUpdate = (data) => {
  //   setCurrentUser(data);
  //   console.log('user data updated');
  // };

  useEffect(() => {
    const checkTokenIsValid = async () => {
      //
      const token = localStorage.getItem('token');
      if (!token) {
        setIsAuthenticated(false);
        setPayload(null);
        return;
      }
    };
    checkTokenIsValid();
    //
  }, [pathname]);
  return (
    <AuthContext.Provider
      value={{
        isAuthenticated,
        //  localStorage  token 
        currentMember: () => {
          const token = localStorage.getItem('token');
          // return jwt_decode(token);
          if (token) return jwt_decode(token);
          //  localStorage  token 
          return {
            id: '',
            email: '',
            account: '',
            name: '',
            avatar: '',
          };
        },
        register: async (data) => {
          //  success: true
          const { success } = await register({
            account: data.account,
            name: data.name,
            password: data.password,
            email: data.email,
            checkPassword: data.checkPassword,
          });
          //  token 
          return success;
        },
        login: async (data) => {
          const { token, success } = await login({
            account: data.account,
            password: data.password,
          });
          const tempPayload = jwt_decode(token);
          if (tempPayload) {
            setPayload(tempPayload);
            setIsAuthenticated(true);
            localStorage.setItem('token', token);
          } else {
            setPayload(null);
            setIsAuthenticated(false);
          }
          return success;
        },
        adminLogin: async (data) => {
          const { token, success } = await adminLogin({
            account: data.account,
            password: data.password,
          });
          const tempPayload = jwt_decode(token);
          if (tempPayload) {
            setPayload(tempPayload);
            setIsAuthenticated(true);
            localStorage.setItem('token', token);
          } else {
            setPayload(null);
            setIsAuthenticated(false);
          }
          return success;
        },
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};
