import { changeUserInformation } from 'api/user';
import AuthInput from 'components/AuthInput';
import Alert from 'components/common/Alert';
import { AuthInputContainer } from 'components/common/auth.styled';
import { Header, ThemeButton } from 'components/common/common.styled';
// import { useAuth } from 'contexts/AuthContext';
import { useUser } from 'contexts/UserContext';
import { useState } from 'react';
import styled from 'styled-components';

const UserSetting = () => {
  const { handleUserUpdate, currentUser } = useUser();
  const nextUser = { ...currentUser };
  const { account: userAccount, name: userName, email: userEmail } = nextUser;
  const [account, setAccount] = useState(userAccount);
  const [name, setName] = useState(userName);
  const [email, setEmail] = useState(userEmail);
  const [password, setPassword] = useState('');
  const [checkPassword, setCheckPassword] = useState('');
  const [showErrorMsg, setShowErrorMsg] = useState('');
  const [showSuccessMsg, setShowSuccessMsg] = useState(false);
  // const nameLength = name.length;
  const StyledDiv = styled.div`
    position: absolute;
    top: 10%;
    left: 50%;
    display: grid;
    place-items: center;
  `;
  const handleClick = async () => {
    if (
      account.length === 0 ||
      name.length === 0 ||
      email.length === 0 ||
      password.length === 0 ||
      checkPassword.length === 0
    ) {
      setShowErrorMsg('!');
      setTimeout(() => {
        setShowErrorMsg(false);
      }, 1000);
      return;
    }
    if (password !== checkPassword) {
      setShowErrorMsg('!');
      setTimeout(() => {
        setShowErrorMsg(false);
      }, 1000);
      return;
    }
    const token = localStorage.getItem('token');
    if (!token) {
      setShowErrorMsg('unauthorized');
      setTimeout(() => {
        setShowErrorMsg(false);
      }, 1000);
      return;
    }
    //  password & checkPassword
    const { data, status } = await changeUserInformation({
      userId: currentUser.id,
      account,
      name,
      email,
      password,
      checkPassword,
    });
    console.log(currentUser.id);
    if (data && status === 200) {
      setShowSuccessMsg(true);
      setTimeout(() => {
        setShowSuccessMsg(false);
      }, 1000);
    }
    // data currentUser
    const newCurrentUser = {
      ...currentUser,
      account: data.account,
      name: data.name,
      email: data.email,
      password: data.password,
      checkPassword: data.password,
    };
    handleUserUpdate(newCurrentUser);
  };
  return (
    <>
      <Header></Header>
      <AuthInputContainer>
        {showErrorMsg && (
          <StyledDiv>
            <Alert type='error' message={showErrorMsg} />
          </StyledDiv>
        )}
        {showSuccessMsg && (
          <StyledDiv>
            <Alert type='success' message='' />
          </StyledDiv>
        )}
        <AuthInput
          label=''
          placeholder=''
          value={account}
          onChange={(accountInputValue) => setAccount(accountInputValue)}
        />
      </AuthInputContainer>
      <AuthInputContainer>
        <AuthInput
          label=''
          placeholder=''
          value={name}
          onChange={(nameInputValue) => setName(nameInputValue)}
        />
      </AuthInputContainer>
      <AuthInputContainer>
        <AuthInput
          type='email'
          label='Email'
          placeholder=' Email'
          value={email}
          onChange={(emailInputValue) => setEmail(emailInputValue)}
        />
      </AuthInputContainer>

      <AuthInputContainer>
        <AuthInput
          type='password'
          label=''
          value={password}
          placeholder=''
          onChange={(passwordInputValue) => setPassword(passwordInputValue)}
        />
      </AuthInputContainer>
      <AuthInputContainer>
        <AuthInput
          type='password'
          label=''
          value={checkPassword}
          placeholder=''
          onChange={(passwordInputValue) =>
            setCheckPassword(passwordInputValue)
          }
          // 
          pattern={`^${password}$`}
          // required 
          required
        />
        {checkPassword !== password ? (
          <span style={{ color: 'var(--color-theme)' }}>
            
          </span>
        ) : (
          ''
        )}
      </AuthInputContainer>
      <ThemeButton width='88px' onClick={handleClick}>
        
      </ThemeButton>
    </>
  );
};

export default UserSetting;
